<!-- by 添少 -->
<template>
  <section style="background: #fff;">
    <mt-header title="发帖">
      <router-link to="/" slot="left">
        <mt-button icon="back" @click="$router.go(-1)"></mt-button>
      </router-link>
      <mt-button @click="handNext" slot="right">发送</mt-button>
    </mt-header>
    <mt-field placeholder="请输入帖子内容" type="textarea" rows="4" v-model="myContent"></mt-field>
    <section class="m-uploads">
      <template v-for="(item,$index) in myPreimg">
        <file-image2
          :src="item.url"
          @load="(e,file)=>handLoadImg(e,file,item)"
          @delete="handDeleteImage(item,$index)">
        </file-image2>
      </template>
    </section>
  </section>
</template>
<script>
  import {mapState} from 'vuex'
  import FixedLayout from '@/components/fixed-layout'
  import FileImage2 from '@/components/file-image2.vue'
  import ImageCompressor from 'image-compressor.js'

  export default {
    name: '',
    components: {
      FixedLayout,
      FileImage2,
    },
    props: {},
    data() {
      return {
        myPreimg: [],
        myContent: '',
      }
    },
    watch: {},
    computed: {
      ...mapState({
        tagId: state => state.write.post.tagId,
        clubId: state => state.write.post.clubId,
        content: state => state.write.post.content,
        // preimg: state => state.write.post.preimg,
      })
    },
    created() {
      this.init()
    },
    mounted() {
    },
    methods: {
      init() {
        for (let i = 0; i < 3; i++) {
          this.myPreimg.push({
            url: '',
            file: null,
          })
        }
      },
      initData() {
      },
      handLoadImg(e, file, item) {
        this.$commons.compressorImage(file).then(res => {
          // item.url = window.URL.createObjectURL(res)
          this.$commons.uploadImage(res).then(res => {
            if (res.code === 200) {
              item.url = res.data
            }
          })
        })
      },
      handDeleteImage(item, index) {
        item.file = ''
        item.url = ''
      },
      handNext() {
        this.$store.commit(this.$actionType.set_post_data, {
          content: this.myContent,
          preimg: this.myPreimg
        })
        if (!this.content) {
          alert('请输入内容')
          return
        }
        let params = {
          clubId: this.clubId,
          tagId: this.tagId,
          cont: this.myContent,
          preimg: (() => {
            let res = ''
            this.myPreimg.forEach(e => e.url && (res += `${e.url}#`))
            return res
          })()
        }
        this.$http.post(this.$port.POST_DISCUSS, this.$qs.stringify(params)).then(res => {
          if (res.code === 200) {
            this.$router.push({
              path: '/forumPage'
            })
            return
          }
          alert('发帖失败')
        })
      },
    }
  }
</script>
<style lang="scss">

</style>
<style lang="scss" scoped>
  .fixed-layout {
    background-color: #fff;
  }

  #editor {
    font-size: 16px;
  }

  .m-uploads {
    display: flex;
    flex-wrap: wrap;
    > section {
      margin-left: 10px;
      margin-bottom: 10px;
      position: relative;
    }
  }

  .m-write {
    margin-bottom: 10px;
  }
</style>



